import React, { Component } from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import {Link} from 'react-router-dom';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Image from 'react-bootstrap/Image';
import logo from '../img/LogoBeyaz.png';
import '../topMenu.css';
import flagTr from '../img/tr_flag.png';
import flagEn from '../img/en_flag.png';
import {LanguageContext, Languages} from '../LanguageContext';

const menuEn = {
    Home : "Home",
    News : "News",
    Concerts : "Concerts",
    Biography : "Biography",
    SopranoBiography : "Soprano Biography",
    SoundHealingBiography : "Sound Healing Biography",
    Milestones : "Milestones",
    SopranoMilestones : "Soprano Milestones",
    SoundHealingMilestones : "Sound Healing Milestones",
    SoundHealing : "Sound Healing",
    WhatIsSoundHealing : "What Is Sound Healing?",
    Seminars : "Seminars / Workshops",
    Repertoire : "Repertoire",
    Projects : "Projects",
    SocialResponsibility : "Social Responsibility",
    Media : "Media",
    Pictures : "Pictures",
    Press : "Press",
    Video : "Video",
    Contact : "Contact"
}

const menuTr = {
    Home : "Anasayfa",
    News : "Haberler",
    Concerts : "Konserler",
    Biography : "Biyografi",
    SopranoBiography : "Soprano Biyografi",
    SoundHealingBiography : "Sesle Şifa Biyografi",
    Milestones : "Kronoloji",
    SopranoMilestones : "Soprano Kronoloji",
    SoundHealingMilestones : "Sesle Şifa Kronoloji",
    SoundHealing : "Sesle Şifa",
    WhatIsSoundHealing : "Sesle Şifa Nedir?",
    Seminars : "Seminerler / Workshops",
    Repertoire : "Repertuar",
    Projects : "Projeler",
    SocialResponsibility : "Sosyal Sorumluluk",
    Media : "Medya",
    Pictures : "Resimler",
    Press : "Basın",
    Video : "Video",
    Contact : "İletişim"
}

const MyNavLink = ({to, className, children}) => {
    return (    
        <Link to={to} className={`nav-link ${className}`}>{children}</Link>
    )
}

const MyDropDownItem = ({to, children}) => {
    return (    
        <Link to={to} className={`menu-item my-dropdown-item`}>{children}</Link>
    )
}

class TopMenu extends Component{
    constructor(props)
    {
        super(props);

        this.state = {
            transparent : this.props.transparent,
            language : this.props.language,
            menuText : this.props.language == Languages.en ? menuEn : menuTr
        }
    }

    componentDidUpdate(previousProps, previousState)
    {        
        if (previousProps !== this.props) {
            this.setState({                
                transparent : this.props.transparent,
                language : this.props.language,
                menuText : this.props.language === Languages.en ? menuEn : menuTr
            });
        }
    }

    render() {
        let menuText = this.state.menuText;
        const topMenuClass = this.state.transparent ? "main-menu-container-transparent" : "";
        return (
            <>
            <LanguageContext.Consumer>
            {({siteLanguage, toggleLanguage}) => ( 
            <Navbar className={`main-menu-container ${topMenuClass}`} expand="lg" fixed="top">
                <Navbar.Brand href="#home">
                    <Image
                        src={logo}                                        
                        className="d-inline-block align-top main-logo"       
                        alt="Gonca Gurses"                                                
                    />
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav" className="justify-content-end"> 
                    <Nav style={{color: 'white'}} className="justify-content-end">
                        <MyNavLink to="/">{menuText.Home}</MyNavLink>
                        <MyNavLink to={`/news/${siteLanguage}`}>{menuText.News}</MyNavLink>
                        <MyNavLink to="/concerts">{menuText.Concerts}</MyNavLink>
                        <NavDropdown title={menuText.Biography} id="collasible-nav-dropdown" style={{backgroundColor:'red !important'}}>
                            <MyDropDownItem to="/BioSoprano">{menuText.SopranoBiography}</MyDropDownItem>
                            <MyDropDownItem to="/BioSoundHealing">{menuText.SoundHealingBiography}</MyDropDownItem>
                            <NavDropdown drop="right" title={menuText.Milestones} id="collasible-nav-dropdown" style={{paddingLeft:"15px"}}>
                                <MyDropDownItem to="/MilestonesSoprano">{menuText.SopranoMilestones}</MyDropDownItem>
                                <MyDropDownItem to="/MilestonesSoundHealing">{menuText.SoundHealingBiography}</MyDropDownItem>
                            </NavDropdown>
                        </NavDropdown>
                        <NavDropdown title={menuText.SoundHealing} id="collasible-nav-dropdown">
                            <MyDropDownItem to="/SoundHealing">{menuText.WhatIsSoundHealing}</MyDropDownItem>
                            <MyDropDownItem to="/Seminars">{menuText.Seminars}</MyDropDownItem>
                        </NavDropdown>
                        <MyNavLink to="/Repertoire/classical">{menuText.Repertoire}</MyNavLink>
                        <NavDropdown title={menuText.Projects} id="collasible-nav-dropdown">
                            <MyDropDownItem to="/SocialResponsibility">{menuText.SocialResponsibility}</MyDropDownItem>
                        </NavDropdown>
                        <NavDropdown title={menuText.Media} id="collasible-nav-dropdown">
                            <MyDropDownItem to="/Media/Pictures">{menuText.Pictures}</MyDropDownItem>
                            <MyDropDownItem to="/Gallery/Press">{menuText.Press}</MyDropDownItem>
                            <MyDropDownItem to="/Videos">{menuText.Video}</MyDropDownItem>
                        </NavDropdown>
                        <MyNavLink to="/Contact">{menuText.Contact}</MyNavLink>
                    </Nav>
                   
                    {siteLanguage == Languages.en &&
                    <Image src={flagTr} height="30px" onClick={toggleLanguage} style={{cursor:"pointer"}}></Image> }
                    {siteLanguage == Languages.tr &&
                    <Image src={flagEn} height="30px" onClick={toggleLanguage} style={{cursor:"pointer"}}></Image> }                  

                </Navbar.Collapse>
            </Navbar>
            )}
            </LanguageContext.Consumer>
            </>
        )
    }
}

TopMenu.contextType = LanguageContext;

export default TopMenu;