import React, { Component } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import gosteri from './img/gonca-gosteri.jpg';
import solo from './img/gonca-solo.jpg';
import toplu from './img/gonca-toplu.jpg';
import './Pictures.css';
import { Languages, LanguageContext } from './LanguageContext.js';

var images = require.context('./img', true);



class Pictures extends Component{
    render(){
        return(
            <LanguageContext.Consumer>
            {({siteLanguage, toggleLanguage}) => ( 
            <>
                <Container className="Main-Container"  style={{paddingLeft:"30px", paddingRight:"10px"}}>                    
                    <Row>
                        <Col className="header-color">
                            <div className="wrapper">
                                <div className="header-left-line header-border"></div>
                                <div className="header-text">
                                {siteLanguage == Languages.en && <h4>PICTURES</h4>}
                                {siteLanguage == Languages.tr && <h4>RESİMLER</h4>}
                                </div>                                
                                <div className="header-right-line header-border"></div>
                            </div>
                        </Col>
                    </Row>
                    <div style={{height:"10px"}}></div>

                    <Row>
                        <Col style={{paddingTop:"20px", paddingBottom:"30px", paddingLeft:"30px", paddingRight:"20px"}}>     
                            <Container>
                                <Row>
                                    <Col lg={4}>
                                    <a href="/gallery/performance">
                                    <div className={"frame"}>
                                        <div className={"frame-text"}>
                                            {siteLanguage == Languages.en && <>Performance</>}
                                            {siteLanguage == Languages.tr && <>Gösteri</>}

                                            
                                        </div>
                                        <img
                                                src={gosteri}
                                                className="img-fluid hover-image"
                                                alt=""
                                            />
                                    </div>
                                    </a>
                                    </Col>
                                    <Col lg={4}>
                                    <a href="/gallery/solo">
                                    <div className={"frame"}>
                                        <div className={"frame-text"}>
                                            Solo
                                        </div>
                                        <img
                                                src={solo}
                                                className="img-fluid hover-image"
                                                alt=""
                                            />
                                    </div>
                                    </a>
                                    </Col>    
                                    <Col lg={4}>
                                    <a href="/gallery/group">
                                    <div className={"frame"}>
                                        <div className={"frame-text"}>
                                            {siteLanguage == Languages.en && <>Group</>}
                                            {siteLanguage == Languages.tr && <>Grup</>}
                                        </div>
                                        <img
                                                src={toplu}
                                                className="img-fluid hover-image"
                                                alt=""
                                            />
                                    </div>
                                    </a>
                                    </Col>                                                                      
                                </Row>
                            </Container>                   
                        </Col>
                    </Row>
                </Container>
            </>
            )}
            </LanguageContext.Consumer>
        );
    }
}

export default Pictures;