import React, { Component } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import {Languages, LanugageContext, LanguageContext} from './LanguageContext';

var images = require.context('./img', true);

class ImageGallery extends Component{

    render(){
        return(
            <LanguageContext.Provider>
            {(siteLanguage, toggleLanguage) => (
            <>
                <Container className="Main-Container"  style={{paddingLeft:"30px", paddingRight:"10px"}}>                    
                    <Row>
                        <Col className="header-color">
                            <div className="wrapper">
                                <div className="header-left-line header-border"></div>
                                <div className="header-text"><h4>
                                {siteLanguage === Languages.en && <>VIDEOS</>}
                                {siteLanguage === Languages.tr && <>VIDEOLAR</>}
                                </h4></div>                                

                                <div className="header-right-line header-border"></div>
                            </div>
                        </Col>
                    </Row>
                    <div style={{height:"10px"}}></div>

                    <Row>
                        <Col style={{paddingTop:"20px", paddingBottom:"30px", paddingLeft:"30px", paddingRight:"20px"}}>
                        <Container>
                            <Row>
                                <Col>
                                </Col>
                            </Row>
                        </Container>
                        </Col>
                    </Row>
                </Container>
            </>
            )}
            </LanguageContext.Provider>            
        );
    }
}

export default ImageGallery;