import React, { Component } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Calendar from "react-big-calendar";
import moment from "moment";
import './Calendar.css';

import "react-big-calendar/lib/css/react-big-calendar.css";

const localizer = Calendar.momentLocalizer(moment);

class Concerts extends Component{

    state = {
        events: [
          {
            start: new Date(),
            end: new Date(moment().add(0, "days")),
            title: "Some concert coming"
          },
          {
            start: new Date(moment().add(3, "days")),
            end: new Date(moment().add(3, "days")),
            title: "Another Concert"
          }
        ]
      };

    render() {
        return (
          <>
            <div>
            <Container className="Main-Container">
              <Row>
                <Col lg={3} className={'d-none d-lg-block d-xl-block'}>
                          <div style={{fontSize:'12px',fontWeight:'bold', color:'#a5a5a5'}}>UPCOMING CONCERTS</div>           
                </Col>
                <Col>
                  <Calendar
                  localizer={localizer}
                  defaultDate={new Date()}
                  defaultView="month"
                  events={this.state.events}
                  style={{ height: "100vh" }}
                  />
                
                </Col>
              </Row>
            </Container>
            </div>
          </>
        );
    }
}

export default Concerts;