import React, { Component, Suspense } from 'react';
import { Route, BrowserRouter as Router, withRouter } from "react-router-dom";
import {createBrowserHistory} from "history"
import './index.css';
import TopMenu from './Components/TopMenu';
import Footer from './Components/Footer';

import News from './News';
import NewsDetails from './NewsDetails';
import Concerts from './Concerts';
import BioSoprano from './BioSoprano';
import BioSoundHealing from './BioSoundHealing';
import MilestonesSoprano from './MilestonesSoprano';
import MilestonesSoundHealing from './MilestonesSoundHealing';
import SoundHealing from './SoundHealing';
import Repertoire from './Repertoire';
import Pictures from './Pictures';
import ImageGallery from './ImageGallery';
import Videos from './Videos';
import Contact from './Contact';
import {LanguageContext, Languages} from './LanguageContext';

const Home = React.lazy(() => import('./Home'));
const history = createBrowserHistory()

class InnerApp extends Component{  
    render(){
        const currentRoute = this.props.location.pathname === "/" ? "body-home" : "";     
        const transparentTopMenu = this.props.location.pathname === "/" ? true : false;          
        return (
            <LanguageContext.Consumer>
            {({siteLanguage, toggleLanguage}) => ( 
            <div className={`App Site ${currentRoute}`}>
                <div className='Site-content'>
                    <div className='App-header' style={{height:"80px"}}>                                            
                        <TopMenu transparent={transparentTopMenu} language={siteLanguage}/>                 
                    </div>
                    <Suspense fallback={<div>Loading...</div>}>
                        <Route path="/" exact component={Home} />
                    </Suspense>
                    <Route path="/news" render={() => <News language={this.props.language}/>}/>
                    <Route path="/newsDetails/:id" component={NewsDetails} />
                    <Route path="/concerts" component={Concerts} />
                    <Route path="/biosoprano" component={BioSoprano} />
                    <Route path="/biosoundhealing" component={BioSoundHealing} />
                    <Route path="/milestonessoprano" render={() => <MilestonesSoprano language={this.props.language}/>}/>
                    <Route path="/milestonessoundhealing" component={MilestonesSoundHealing} />
                    <Route path="/soundhealing" component={SoundHealing} />
                    <Route path="/repertoire/:id" render={(props) => <Repertoire {...props} language={this.props.language} />} />
                    <Route path="/media/pictures" exact component={Pictures} />
                    <Route path="/gallery/:id" exact render={(props) => <ImageGallery {...props} language={this.props.language}/>} />
                    <Route path="/videos" exact component={Videos} />
                    <Route path="/contact" exact component={Contact} />
                </div>
                <Footer />
            </div>
            )}
            </LanguageContext.Consumer>
        )
    }
}
const InnerAppRouted = withRouter(InnerApp)

class App extends Component{  
    render() {       
        return (        
        <>		
            <Router history={history}>
                <InnerAppRouted language={this.props.language}/>
            </Router>
        </>
        )
    }
}

export default App;