import React, { Component } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

class LineItem extends Component{
    constructor(props){
        super(props);
        this.state = {
            hover: false,
            hideBorder : this.props.hideBorder,
            noHover : this.props.noHover
        };

        this.styles = {minhHeight:"30px", paddingTop:"3px", fontSize:"14px"};

        if(!this.props.hideBorder)
        {
            this.styles.borderBottom = "1px #eaeaea solid";
        }

        if(this.props.alternate)
        {
            this.styles.backgroundColor = "#ededed";
        }

        if(this.props.noHover)
        {
            this.styles.paddingBottom = "10px";
            this.styles.fontWeight = "bold";
            this.styles.color = "#555555"
        }
    }

    hoverOn = () => {
        this.setState({
            hover: true
        });
    }

    hoverOff = () => {
        this.setState({
            hover: false
        });
    }    

    
    render() {

        return (
            <Container style={this.styles} onMouseEnter={this.hoverOn} onMouseLeave={this.hoverOff} className={this.state.hover && !this.state.noHover ? "line-item-hover" : ""}>
                <Row>
                    <Col lg={4}>
                        {this.props.text1}
                    </Col>
                    <Col lg={4}>
                        {this.props.text2}
                    </Col>
                    <Col lg={4}>
                        {this.props.text3}
                    </Col>
                </Row>
            </Container>
        );    
    }
}

export default LineItem;