import React, { Component } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';
import HeaderWithLines from './Components/HeaderWithLines';
import SubHeaderWithLines from './Components/SubHeaderWithLines';
import Separator from './Components/Separator';
import LineItem from './Components/LineItem';
import { Languages, LanguageContext } from './LanguageContext.js';
import {Link} from 'react-router-dom';

const repertoireEn = require('./json/repertoire-eng.json');
const repertoireTr = require('./json/repertoire-tr.json');

class Repertoire extends Component{
    constructor(props){
        super(props);

        console.log(props);

        this.categoryId = props.match.params.id;

        this.state = {
            repertoire: this.props.language === Languages.en ? 
            repertoireEn.categories.find((category) => category.id == this.categoryId) :
            repertoireTr.categories.find((category) => category.id == this.categoryId) 
        }            
    }   

    componentDidUpdate(previousProps, previousState)
    {                
        console.log("Did update");
        console.log(this.props);
        if (previousProps !== this.props) {
            
            this.categoryId = this.props.match.params.id;
            
            console.log(this.props.language);

            this.setState({
                repertoire: this.props.language === Languages.en ? 
                repertoireEn.categories.find((category) => category.id == this.categoryId) :
                repertoireTr.categories.find((category) => category.id == this.categoryId) 
                });
        }
    }

    render(){
        return(
            
            <LanguageContext.Consumer>
            {({siteLanguage, toggleLanguage}) => ( 
            <>
                <Container className="Main-Container"  style={{paddingLeft:"30px", paddingRight:"30px"}}>   
                    <Row>
                        <Col lg={3}>
                            {siteLanguage == Languages.en &&
                            <Container>
                                <Row>
                                    <Col>
                                        <Link to="/Repertoire/classical">Classical</Link>                                        
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>
                                        <Link to="/Repertoire/musical">Musical</Link>                                           
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>
                                    <Link to="/Repertoire/christmas">Christmas Songs</Link>   
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>
                                        <Link to="/Repertoire/standards">Standards</Link>       
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>
                                        <Link to="/Repertoire/turkish">Turkish Songs</Link>   
                                    </Col>
                                </Row>
                                <Row>                                
                                    <Col>
                                        <Link to="/Repertoire/crossover">Crossover Songs</Link>   
                                    </Col>                                    
                                </Row>
                            </Container>
                            }
                            {siteLanguage == Languages.tr &&
                            <Container>
                                <Row>
                                    <Col>
                                        <Link to="/Repertoire/classical">Klasik</Link>   
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>
                                        <Link to="/Repertoire/musical">Müzikal</Link>   
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>
                                        <Link to="/Repertoire/christmas">Noel Şarkıları</Link>   
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>
                                        <Link to="/Repertoire/standards">Standartlar</Link>   
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>
                                        <Link to="/Repertoire/turkish">Türk Eserleri</Link>   
                                    </Col>
                                </Row>
                                <Row>                                
                                    <Col>
                                        <Link to="/Repertoire/crossover">Popüler Şarkılar</Link>   
                                    </Col>                                    
                                </Row>
                            </Container>
                            }                            
                        </Col>
                        <Col>
                            <HeaderWithLines text={this.state.repertoire.name}></HeaderWithLines>
                            {this.state.repertoire.items.map(function(item){
                                return(<>
                                    {item.subHeader && 
                                        <SubHeaderWithLines text={item.subHeader}></SubHeaderWithLines>
                                    }

                                    {( item[1] || item[2] || item[3] ) && 
                                    <LineItem text1={item[1]} text2={item[2]} text3={item[3]} hideBorder={item.hideBorder != undefined} noHover={item.noHover != undefined ? item.noHover : false}></LineItem>
                                    }

                                    {item.separator && 
                                        <Separator></Separator>
                                    }
                                </>);
                            })}                            
                        </Col>
                    </Row>                 
                </Container>
            </>
            )}
            </LanguageContext.Consumer>            
        );
    }
}

export default Repertoire;