import React, { Component } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';
import { Link } from "react-router-dom";
import { Languages, LanguageContext } from './LanguageContext.js';

var images = require.context('./img', true);
const newsData = require('./json/news.json');
const leftMenuClasses = 'hidden-md hidden-sm hidden-xs'

class News extends Component{
    constructor(props){
        super(props);

        let newsFiltered = newsData.data.filter((news) => news.language === this.props.language.toString());
        
        console.log(newsFiltered.filter((news) => news.showInArchive === true));

        this.state = {
            newsLeft: newsFiltered.filter((news) => news.showInArchive === true),
            news: newsFiltered.filter((news) => news.showInMain === true)
        }            
    }   

    componentDidUpdate(previousProps, previousState)
    {        
        if (previousProps !== this.props) {
            console.log( this.props.language === Languages.en);
            let newsFiltered = newsData.data.filter((news) => news.language === this.props.language.toString());

            this.setState({                
                newsLeft: newsFiltered.filter((news) => news.showInArchive === true),
                news: newsFiltered.filter((news) => news.showInMain === true)
            });
        }
    }

    render(){
        console.log(this.state);
        return(
            <>
            <LanguageContext.Consumer>
            {({siteLanguage, toggleLanguage}) => ( 
            <Container className='Main-Container'>                
                <Row>
                    <Col lg={3} className={'d-none d-lg-block d-xl-block'}>
                        <div style={{fontSize:'12px',fontWeight:'bold', color:'#a5a5a5'}}>
                            {siteLanguage === Languages.en && <div>ARCHIEVE</div>}
                            {siteLanguage === Languages.tr && <div>ARŞİV</div>}
                        </div>                        
                        {this.state.newsLeft.map(function(item, key){
                            return(
                                <>
                                <div style={{height:'10px'}}></div>
                                <Container>
                                    <Row>
                                        <Col lg={2}>
                                            { item.image === '' ? null :
                                            <Image src={images(item.image)} style={{width:'30px'}}></Image> }
                                        </Col>
                                        <Col lg={10}>
                                            <div>
                                                <div style={{color:'#1bc6d3'}}>
                                                    <b>{item.header}</b>
                                                </div>
                                                <div style={{fontSize:'13px', paddingTop:'3px', paddingBottom:'4px', color:'#858585'}}><b>{item.date}</b></div>                                                
                                                <div style={{fontSize:'14px', color:'#a5a5a5'}}>
                                                    {item.summary}
                                                </div>
                                                { item.location === '' ? null :
                                                    <div style={{fontSize:'12px', paddingTop:'5px', color:'#b5b5b5', fontWeight:'bold'}}>{item.location}</div>
                                                }           
                                                { item.location === '' ? null :
                                                    <div style={{fontSize:'13px', paddingTop:'5px', color:'#858585'}}><i>{item.time}</i></div>
                                                }                                                                                      
                                            </div>
                                            <div style={{height:'15px'}}></div>
                                        </Col>                        
                                    </Row>
                                </Container>
                                </>
                            )
                        })}
                    </Col>
                    <Col lg={6}>
                    
                        <div style={{display:'flex'}}>
                            <div style={{ minWidth:'110px', color:'#444444'}}>
                                {siteLanguage === Languages.en && <div>Latest News</div>}
                                {siteLanguage === Languages.tr && <div>Son Haberler</div>}
                            </div>
                            <div style={{width:'100%', height:'2px', position:'middle',marginTop:'10px', backgroundColor:'#1bc6d3'}}></div>
                        </div>

                        {this.state.news.map(function(item, key){
                            return(
                                <>
                                <div style={{height:'20px'}}></div>
                                <Container style={{width:'100%'}}>
                                    <Row>
                                        <Col>
                                        { item.image === '' ? null : <>
                                            <div style={{width:'400px', height:'266px'}}>
                                            <Link to={`/NewsDetails/${item.id}`} style={{color:'inherit', textDecoration:'none'}}>
                                            <Image src={images(item.image)} style={{maxWidth:'500px', clip:'rect(0px,400px,266px,0px)', position:'absolute'}}></Image>
                                            </Link>
                                            </div>
                                            <div style={{height:'20px'}}></div> </>
                                        }                                        
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col>
                                            <div>
                                                <div className="header-color">
                                                <Link to={`/NewsDetails/${item.id}`} className="styled-link"><h4>{item.header}</h4></Link>
                                                </div>
                                                <div style={{fontSize:'16px', color:'#666'}}>
                                                    {item.description}
                                                </div>
                                            </div>
                                            <div style={{height:'25px'}}></div>
                                        </Col>                        
                                    </Row>
                                </Container>
                                </>
                            )
                        })}                    
                    
                    
                    </Col>
                    <Col lg={2}></Col>
                </Row>        
            </Container>
            )}
            </LanguageContext.Consumer>
            </>
        );
    }
}

export default News;