import React, { Component } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Languages, LanguageContext } from './LanguageContext.js';

const milestoneEnData = require('./json/milestone-soprano-eng.json');
const milestoneTrData = require('./json/milestone-soprano-tr.json');

class MilestonesSoprano extends Component{
    
    constructor(props){
        super(props);

        this.state = {
            milestoneData: this.props.language === Languages.en ? milestoneEnData : milestoneTrData        
        }            
    }   

    componentDidUpdate(previousProps, previousState)
    {                
        if (previousProps !== this.props) {
            console.log(this.props.language);
            this.setState({
                milestoneData: this.props.language === Languages.en ? milestoneEnData : milestoneTrData        
            });
        }
    }

    render(){
        return(
            <>
                <LanguageContext.Consumer>
                {({siteLanguage, toggleLanguage}) => ( 
                <Container className="Main-Container"  style={{paddingLeft:"30px", paddingRight:"10px"}}>                    
                    <Row>
                        <Col className="header-color">
                            <div className="wrapper">
                                <div className="header-left-line header-border"></div>
                                <div className="header-text">
                                {siteLanguage === Languages.en && <h4>SOPRANO MILESTONES</h4>}
                                {siteLanguage === Languages.tr && <h4>SOPRANO KRONOLOJİ</h4>}
                                </div>
                                <div className="header-right-line header-border"></div>
                            </div>
                        </Col>
                    </Row>
                    <div style={{height:"10px"}}></div>
                    {this.state.milestoneData.data.map(function(item, key){
                        return(
                            <>
                            <Row>
                                <Col className="header-color">
                                    <div className="wrapper">
                                        <div className="header-left-line content-border"></div>
                                        <div className="header-text content-text">{item.year}</div>                                
                                        <div className="header-right-line content-border"></div>
                                    </div>
                                </Col>
                            </Row>
                            <Row>
                                <Col className="header-color" style={{paddingTop:"20px", paddingBottom:"20px", fontSize:"14px"}}>
                                    <div className="wrapper">
                                        <div className="header-left-line"></div>
                                        <div className="header-text content-text">{item.description.split('\n').map(line => <p>{line}</p>)}</div>                                
                                        <div className="header-right-line"></div>
                                    </div>
                                </Col>
                            </Row>                            
                            </>
                        )
                    })}
                </Container>
                )}
                </LanguageContext.Consumer>
            </>
        );
    }
}

export default MilestonesSoprano;