import React, { Component } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';
import foto from './img/sesleSifaFoto.jpg';
import { Languages, LanguageContext } from './LanguageContext.js';

class BioSoundHealing extends Component{

    render(){
        return(
            <>
                <LanguageContext.Consumer>
                {({siteLanguage, toggleLanguage}) => ( 

                <Container className="Main-Container">
                    <Row>
                        <Col className="header-color" style={{paddingLeft:"30px"}}>
                             { siteLanguage === Languages.en && <h3>Sound Healing</h3> }
                             { siteLanguage === Languages.tr && <h3>Sesle Şifa</h3> }
                        </Col>
                    </Row>
                    <Row>
                        <Col lg={4} style={{paddingLeft:"30px", paddingTop:"20px"}}>
                            <Image src={foto} fluid></Image>
                        </Col>
                        <Col style={{paddingTop:"20px", paddingBottom:"30px", paddingLeft:"30px", paddingRight:"20px"}}>        
                        { 
                            siteLanguage === Languages.en &&
                            <div>                
                            She was born in Münich, Germany. She is the grand-daughter of Hafız Burhan Gürses who was the singing teacher of the famous Turkish Classical Music singer, Hamiyet Yüceses. He sang in the İstanbul Radio House during the period of Münir Nurettin Selçuk. His singing teacher gave him the “Gürses” (strong, flowing voice) surname during the Surname Law.
                            <br /><br />
                            Since primary school she had sung in choirs. In 1992, she was graduated from 9 September University, English Teaching as a Second Language, BA diploma. In 1993, she started singing in İzmir French Cultural Association Classical Music Choir and also started taking singing lessons from Sabahat Tekebaş. Between 1995-2001 she studied Performing Arts in Opera and Musical in İstanbul Mimar Sinan State Conservatory, BM diploma. During her study she worked with the following singing coaches; Lynn Trepel Çağlar, Azra Gün, Payam Koryak.
                            <br /><br />
                            Since 1995, she has been giving Classical Music, Christmas Carol and Broadway Musical Concerts in French, Austrian, Italian Cultural Centers, Bosphorus Univ., Robert College etc.
                            <br /><br />
                            Between 1996-2004, she worked for İstanbul State Opera House as a contracted solo singer, her performances were:<br />
                            The lead role Nina in the “Nutcracker” children musical based on Tchaikovsky’s music.<br />
                            Conservatory graduation complete opera lead role Musetta from “La Bohème” Opera, Puccini and Fauré Requiem, soprano solo with MSÜ Orchestra, conducted by Fahrettin Kerimov.<br />
                            “Do you like Mozart?”, sang “The Magic Flute” Queen of the Night.<br />
                            “Telephone” comic opera, lead role Lucy.<br />
                            In 1999, she attended a Bel Canto singing workshop of Leyla Gencer in Atatürk Cultural Centre.
                            <br /><br />
                            She sang classical and musical repertoire in “Turkuaz Music Group” touring around and out of Turkey.
                            In 2003, she attended a vocal singing workshop of Eva Blahová “Wiener Musik Seminar” in Vienna and gave a recital in Bösendorfer Saal.
                            <br /><br />
                            In 2006, she attended the workshop of French percussionist Le Quan Ninh “Transit-İstanbul Improvisation Days in Music”. She was invited by “Islak Köpek ” Group for vocal improvisation in their performance. She also did a vocal improvisation for the improvisational dance performance in Darphane-i Amire.
                            <br /><br />
                            Apart from being a singer, she worked as an English, music, drama, dance, singing and Turkish Culture teacher in international schools, including her conservatory MSU she was graduated from.
                            In 2003 starting as a hobby, after studying and making research about alternative healing, she studied the healing power of the human voice “Sound Healing” technique with Jonathan Goldman in Boulder, Denver, Colorado and received a sound healer certificate. She is the first and only Turkish Sound Healer in Turkey who is also the member of “World Sound Healers Association”. She continues her sound healing career by giving private and group sessions, workshops and doing vocal improvisation.
                            <br /><br />
                            In 2008, she gave a Christmas Carol Concert in Kuruçeşme Divan, for the Dutch Consulate. For the opening of 2009 Istanbul Biennial, she gave a Classical Concert in the Dutch Consulate. In Sept. 2014, she sang in , “3 Sopranos Classic Summer Recital” in the Dutch Chapel, İstanbul.
                            <br /><br />
                            After a long intermission in her singing career; she got married to the Dutch chef, photographer, documentary program maker Wilco van Herpen and has a daughter whom she has brought up to the age of 6, it is high time to get back to what she loves most, singing…                        
                            </div>
                        }
                        { 
                            siteLanguage === Languages.tr &&
                            <div>                
                            Münih, Almanya’da doğdu. Dokuz Eylül Üniversitesi İngilizce Öğretmenliği’nde öğrenimini tamamladıktan sonra Mimar Sinan Üniversitesi Sahne Sanatları Opera Bölümünde ikinci lisans eğitimini tamamladı. İstanbul Devlet Operası’nda çeşitli eserlerde oynadı ve Grup Turkuaz’ın solistliğini yaptı. Opera solistliğinin yanı sıra MSÜ ve uluslararası birçok okulda İngilizce dersi, İngilizce olarak drama, müzik, dans, drama, şan, Türk kültürü alanlarında dersler verdi.
                            <br /><br />
                            2002 yılından itibaren ezoterik bilgiler ve çeşitli şifa teknikleri üzerine çalışmalarda yaptı. (İnisiyasyonları: Reiki, Taşlarla şifa, Kundalini, Kwan Yin, Ra- Sheeba, Sesle şifa, Gümüş Mor Alev, Full Spectrum Şifa, Altın Üçgen, Yaşam Küresi, Isis Mavi Ay, Kutsal Kase, Şifa çemberi). Uzun süren grup ve özel çalışmaların sonunda tüm bu öğretileri bir araya getirerek kendi tekniğini oluşturdu.
                            <br /><br />
                            2006 yılında Fransız perküsyoncu Le Quan Ninh’in “Transit-İstanbul Improvisation Days in Music \ Müzikte Doğaçlama” atölyesine katıldı. Islak Köpek Grubunun konserinde konuk solist olarak doğaçlama performans gerçekleştirdi. Ayrıca Darphane-i Amire’de doğaçlama dans gösterisinde yine solo doğaçlama söyledi.
                            <br /><br />
                            Aynı yılda, Colorado, Denver, Boulder’da Jonathan Goldman’la ‘ Sound Healing / Sesle Şifa ‘ atölyesine katıldı. Buradan aldığı sertifikayla Türkiye’de bu alanda çalışmalar yapan aynı zamanda Dünya Sesle Şifa Enstitüsüne kayıtlı ilk ve tek Türk Sesle Şifa Terapisti oldu.
                            <br /><br />
                            Büyükada Naya Retreats, Bilgi Paylaşım Merkezi’nde sesle şifa grup terapileri, kişisel seanslar ve bu konuda çeşitli seminerler verdi.
                            <br /><br />
                            14 Şubat’ta, Jonathan Goldman” ın önderliğinde dünya çapında tüm yetiştirdiği öğrencilerinin yapmakta olduğu “Dünyamıza Sesle Şifa Günü” çalışmasını her yıl İstanbul’da yapmaktadır.
                            <br /><br />
                            Halen bireysel seanslar, grup çalışmaları, seminerler vererek ve vokal doğaçlamalar yaparak bu konudaki çalışmalarına devam etmektedir.
                            </div>
                        }
                        </Col>
                    </Row>
                </Container>
                )}
                </LanguageContext.Consumer>
            </>
        );
    }
}

export default BioSoundHealing;