import React, { Component } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Gallery from 'react-grid-gallery';
import { Languages, LanguageContext } from './LanguageContext';

var images = require.context('./img', true);

class ImageGallery extends Component{
    getGalleryName(name, language){
        var turkishNames = {
            "performance" : "Gösteri Resimleri",
            "solo" : "Solo Resimler",
            "group" : "Toplu Resimler",
            "press" : "Basın"
        }
        var englishNames = {
            "performance" : "Performance Pictures",
            "solo" : "Solo Pictures",
            "group" : "Group Pictures",
            "press" : "Press"
        }

        return language == Languages.en ? englishNames[name.toLowerCase()] : turkishNames[name.toLowerCase()];
    }

    constructor(props){
        super(props);
        this.galleryId = props.match.params.id;

        this.closeLightbox = this.closeLightbox.bind(this);
        this.getGalleryName = this.getGalleryName.bind(this);
        this.images = [];

        if(this.galleryId === 'performance')
        {
            this.images = [{
                src: require('./img/gonca-gosteri.jpg'),
                thumbnail: require('./img/gonca-gosteri.jpg')
            }];
        }
        else if(this.galleryId === 'solo')
        {
            this.images = [{
                src: require('./img/gonca-solo.jpg'),
                thumbnail: require('./img/gonca-solo.jpg')
            }];
        }
        else if(this.galleryId === 'group')
        {
            this.images = [{
                src: require('./img/gonca-toplu.jpg'),
                thumbnail: require('./img/gonca-toplu.jpg')
            }];
        }
        else if(this.galleryId === 'Press')
        {
            this.images = [{
                src: require('./img/press-1.jpg'),
                thumbnail: require('./img/press-1.jpg')
            },
            {
                src: require('./img/press-2.jpg'),
                thumbnail: require('./img/press-2.jpg')
            },
            {
                src: require('./img/press-3.jpg'),
                thumbnail: require('./img/press-3.jpg')
            },
            {
                src: require('./img/press-4.jpg'),
                thumbnail: require('./img/press-4.jpg')
            },
            {
                src: require('./img/press-5.jpg'),
                thumbnail: require('./img/press-5.jpg')
            }
        ];
        }

        this.state = {
			lightboxIsOpen: false,
            currentImage: 0,
            galleryName : this.getGalleryName(this.galleryId, this.props.language)
        };
        
        console.log("test");

        console.log(this.galleryId, this.props.language);

        console.log(this.state);
    }


    componentDidUpdate(previousProps, previousState)
    {    
        if(previousProps !== this.props)
        {            
            this.setState(prevState => ({
                ...prevState,
                galleryName : this.getGalleryName(this.galleryId, this.props.language)
            }));
        }
    }

	closeLightbox () {
		this.setState(prevState => ({
            ...prevState,
			currentImage: 0,
			lightboxIsOpen: false,
        }));
    }

    render(){
        return(
            <LanguageContext.Consumer>
            {(siteLanguage, toggleLanguage) => (

            <>
                <Container className="Main-Container"  style={{paddingLeft:"30px", paddingRight:"10px"}}>                    
                    <Row>
                        <Col className="header-color">
                            <div className="wrapper">
                                <div className="header-left-line header-border"></div>
                                <div className="header-text"><h4>{this.state.galleryName.toUpperCase()}</h4></div>                                
                                <div className="header-right-line header-border"></div>
                            </div>
                        </Col>
                    </Row>
                    <div style={{height:"10px"}}></div>

                    <Row>
                        <Col style={{paddingTop:"20px", paddingBottom:"30px", paddingLeft:"30px", paddingRight:"20px"}}>
                        <Gallery enableImageSelection={false} images={this.images}/>                       
                        </Col>
                    </Row>
                </Container>
            </>
            )}
            </LanguageContext.Consumer>
                
        );
    }
}

export default ImageGallery;