import React, { Component } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';
import { Link } from "react-router-dom";
import { Languages, LanguageContext } from './LanguageContext.js';

var images = require.context('./img', true);
const newsData = require('./json/news.json');

class NewsDetails extends Component{
    constructor(props){
        super(props);
        this.newsId = props.match.params.id;
        this.news = newsData.data.find((news) => news.id == this.newsId);

        this.state = {        
        }
    }


    
    render(){
        return(
            <>
            <LanguageContext.Consumer>
            {({siteLanguage, toggleLanguage}) => ( 
            <Container className='Main-Container' style={{color:"#8d8d8d"}}>
                <Row>
                    <Col style={{paddingTop:"10px"}}>
                        <Image src={images(this.news.image)} />
                    </Col>
                </Row>
                <Row>
                    <Col style={{paddingTop:'20px'}} className="header-color">
                        <h3>{this.news.header}</h3>
                    </Col>
                </Row>
                <Row>
                    <Col style={{paddingBottom:"20px"}}>
                        {this.news.details}
                    </Col>
                </Row>
                <Row>
                    <Col>
                    <Container fluid>
                        <Row>
                            <Col style={{paddingBottom:"20px", textAlign:"right", fontSize:"12px"}}>
                            <b>
                            <Link to="/News" className="styled-link">
                            {siteLanguage === Languages.en && <div>BACK</div>}
                            {siteLanguage === Languages.tr && <div>GERİ</div>}
                            </Link>
                            </b>
                            </Col>                            
                        </Row>
                    </Container>
                    </Col>
                </Row>                    
            </Container>
            )}
            </LanguageContext.Consumer>
            </>
        );
    }
}

export default NewsDetails;